import React, { Component } from 'react';
import { createProject } from '../../Components/Actions/projectActions';
import { connect } from 'react-redux';
import { singIn } from '../Actions/authActions'


class CreateProject extends React.Component {
    state ={
        titulo:'',
        contenido:''
    }

    loguear(e){
        e.preventDefault()
        this.props.singIn(this.state)
    }

    handleChange = e => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleSubmit = e => {
        e.preventDefault();
        console.log(this.state)
        this.props.createProject(this.state);
    }

    render() {
        const { authError } = this.props
        return (
            <div className="container">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5>Probando la conexión con banco de datos</h5>
                    <div className="input-field">
                        <label htmlFor="titulo">Título</label>
                        <input onChange={this.handleChange} type="text" id='titulo' />
                    </div>
                    <div className="input-field">
                        <label htmlFor="contenido">Contenido</label>                        
                        <textarea id="contenido" onChange={this.handleChange} className="materialize-textarea"></textarea>
                    </div>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-1">Enviar</button>
                    </div>
                </form>


                <form onSubmit={this.loguear} className="white">
                    <h5>Probando login</h5>
                    <div className="input-field">
                        <label htmlFor="email">Email</label>
                        <input onChange={this.handleChange} type="email" id='email' placeholder="email"/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="password">Password</label>                        
                        <input id="password" onChange={this.handleChange} type="password" placeholder="contraseña"></input>
                    </div>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-1">Loguear</button>
                    </div>
                    <div className="red-text center">
                        {authError ? <p>{authError}</p> : <p>No hay error</p>}
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        authError: state.auth.authError
    }
}

const mapDispatchToProps = dispatch => {
    return {
      createProject: (project) => dispatch(createProject(project)),
      singIn: (creds) => dispatch(singIn(creds))
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(CreateProject)
  